.findOrder {
  min-height: calc(100vh - 70px);
  height: 100%;
  width: 100%;
}

.findOrder * {
  font-family: "Montserrat";
}

.findOrder__container {
  width: 100%;
  height: 100%;
  padding-top: 40px;
}

.findOrder__search-box {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 50px;
  margin: 0 auto;
  border-radius: 30px;
  box-shadow: 1.5px 3px 10px #bbb;
}

.findOrder__searchInput {
  padding: 0 20px;
  height: 100%;
  min-width: 280px;
  flex: 1;
  border: none;
  outline: none;
  font-size: 1.1rem;
  border-radius: 30px 0 0 30px;
}

.findOrder__searchButton {
  border: none;
  background: #fff;
  border-radius: 0 30px 30px 0;
  padding: 0 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background 0.4s linear;
  flex: 0.1;
  color: darkgray;
  cursor: pointer;
  height: 100%;
  outline: none;
}

.findOrder__searchButton:hover {
  background: dodgerblue;
  color: white;
}

.findOrder__searchButton:active {
  background: rgb(1, 100, 199);
}

.findOrder__foundedOrder {
  background: white;
  margin: 80px auto 0 auto;
  transition: background 0.1s linear;
  line-height: 1.5;
  padding: 10px 40px;
}

.findOrder__foundedOrder:hover {
  background: #ddd;
}

.findOrder__foundedOrder-number {
  padding: 10px 0;
  text-decoration: underline;
  color: blue;
}

.findOrder__foundedOrder-name,
.findOrder__foundedOrder-email,
.findOrder__foundedOrder-date {
  font-size: small;
}
